const CV = () => {
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
    setPageNumber(1);
  }

  function changePage(offset) {
    setPageNumber(prevPageNumber => prevPageNumber + offset);
  }

  function previousPage() {
    changePage(-1);
  }

  function nextPage() {
    changePage(1);
  }

  return (
    <GlobalWrapper>
      <Header title="Ignorance of Ecological Costs" />
      <Frame>
          <iframe scrolling="no" src="https://issuu.com/ektarakholiya/docs/ignoranceofec_ebr"  width="100%" height="1000" 
 type="application/pdf"/>
      </Frame>
      <Section>
        <Content>

        </Content>
      </Section>
      <Footer />
    </GlobalWrapper>
  )
}

export default CV
